import React from "react";

function FormInput({ handleChange, formInputData, handleSubmit }) {
  return (
    <React.Fragment>
      <div className="col-sm-6 pb-5">
        <h4 className="text-bold text-center pb-4">Add Book List</h4>
        <div class="mb-3 row">
          <label for="bookid" class="col-sm-2 col-form-label">
            Book Id
          </label>
          <div class="col-sm-10">
            <input
              type="text"
              onChange={handleChange}
              value={formInputData.bookid}
              name="bookid"
              className="form-control"
              
              id="bookid"
            />
          </div>
        </div>

        <div class="mb-3 row">
          <label for="title" class="col-sm-2 col-form-label">
            Title
          </label>
          <div class="col-sm-10">
            <input
              type="email"
              onChange={handleChange}
              value={formInputData.title}
              name="title"
              className="form-control"
              
              id="title"
            />
          </div>
        </div>
        <div class="mb-3 row">
          <label for="author" class="col-sm-2 col-form-label">
            Author
          </label>
          <div class="col-sm-10">
            <input
              type="text"
              onChange={handleChange}
              value={formInputData.author}
              name="author"
              className="form-control"
              
              id="author"
            />
          </div>
        </div>
        <div class="mb-3 row">
          <label for="publication" class="col-sm-2 col-form-label">
            Year Of Publication
          </label>
          <div class="col-sm-10">
            <input
              type="text"
              onChange={handleChange}
              value={formInputData.publication}
              name="publication"
              className="form-control"
              
              id="publication"
            />
          </div>
        </div>

        <div className="col">
          <input
            type="submit"
            onClick={handleSubmit}
            className="btn btn-primary"
            value="Add Book"
          />
        </div>
      </div>
    </React.Fragment>
  );
}

export default FormInput;
