import React, { useEffect, useState } from "react";

import { TbArrowsSort } from "react-icons/tb";
import FormInput from "./FormInput";
import SearchBar from "./SearchBar";
import Table from "./Table";
function DisplayFormData() {
  const [tableData, setTableData] = useState([]);
  const [error, setError] = useState([]);
  const [fulltabledata, setFulltabledata] = useState([]);
  const [formInputData, setformInputData] = useState({
    bookid: "",
    title: "",
    author: "",
    publication: "",
  });
  const [order, setOrder] = useState("ASC");
  const handleChange = (evnt) => {
    const newInput = (data) => ({
      ...data,
      [evnt.target.name]: evnt.target.value,
    });
    setformInputData(newInput);
  };

  const handleSubmit = (evnt) => {
    evnt.preventDefault();
    const checkEmptyInput = !Object.values(formInputData).some(
      (res) => res === ""
    );

    if (checkEmptyInput) {
      const newData = (data) => [...data, formInputData];
      setTableData(newData);
      setFulltabledata(newData);
      const emptyInput = { bookid: "", title: "", author: "", publication: "" };
      setformInputData(emptyInput);
      setError("");
    } else {
      setError("please enter all the details");
    }
  };
  console.log(formInputData);
  useEffect(() => {
    localStorage.setItem("formValues", JSON.stringify(tableData));
  }, [tableData]);
  const filterValue = (message) => {
    if (message === "") {
      console.log("message empty");

      return fulltabledata;
    }
    return fulltabledata.filter(
      (data) =>
        data.author.toLowerCase().includes(message) ||
        data.bookid.toLowerCase().includes(message) ||
        data.title.toLowerCase().includes(message) ||
        data.publication.toLowerCase().includes(message)
    );
  };
  const [message, setMessage] = useState("");

  useEffect(() => {
    const filterr = filterValue(message);
    setTableData(filterr);
  }, [message]);
  const sorting = (col) => {
    console.log("hello");
    if (order === "ASC") {
      const sorted = [...tableData].sort((a, b) =>
        a[col].toLowerCase() > b[col].toLowerCase() ? 1 : -1
      );
      setTableData(sorted);
      setOrder("DSC");
    }
    if (order === "DSC") {
      const sorted = [...tableData].sort((a, b) =>
        a[col].toLowerCase() < b[col].toLowerCase() ? 1 : -1
      );
      setTableData(sorted);
      setOrder("ASC");
    }
  };

  //   console.log(message);
  //   console.log(tableData);
  return (
    <React.Fragment>
      <div className="container pt-5">
        <div className="row">
          <div className="col-sm-12">
            <FormInput
              handleChange={handleChange}
              formInputData={formInputData}
              handleSubmit={handleSubmit}
            />
            {error ? <p style={{ color: "red" }}>{error}</p> : null}
            <div className="row pb-4">
              <div className="col-sm-4">
                <input
                  type="text"
                  className="form-control"
                  placeholder="search bar"
                  onChange={(e) => setMessage(e.target.value)}
                />
              </div>
            </div>
            {/* <Table tableData={tableData} /> */}
            <table className="table table-bordered">
              <thead className="table-primary">
                <tr>
                  <th>S.N</th>
                  <th
                    onClick={() => {
                      sorting("bookid");
                    }}
                  >
                    Book Id <TbArrowsSort/>
                  </th>
                  <th onClick={() => {
                      sorting("title");
                    }}>Title <TbArrowsSort/></th>
                  <th onClick={() => {
                      sorting("author");
                    }}>Author <TbArrowsSort/></th>
                  <th onClick={() => {
                      sorting("publication");
                    }}>Year of publication <TbArrowsSort/></th>
                </tr>
              </thead>
              <tbody>
                {tableData.map((data, index) => {
                  return (
                    <tr key={index}>
                      <td>{index + 1}</td>
                      <td>{data.bookid}</td>
                      <td>{data.title}</td>
                      <td>{data.author}</td>
                      <td>{data.publication}</td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
export default DisplayFormData;
